.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
li::marker {
  font-size: 0px;
}
.authorlist{
  width: 50%;
  margin:auto;
}
.feedbacks{
  width: 50%;
  margin:auto;
  border: 1px solid black;
  margin-top: 50px;
  padding-top: 30px;
}

.wishlist{
  padding-top: 30px;
  margin-bottom: 100px;
}